import "./home.css"
import React, { useEffect, useState } from 'react'
import { useAnimation, motion, animate } from "framer-motion"
import { useInView } from "react-intersection-observer"
import man from "./images/man-pc.svg"
import { FingerPrint, Swipe, Cat, Rct } from "./LottieComponents"
const Home = () => {
    const { ref, inView } = useInView({
        threshold: 0.2
    });
    const animation = useAnimation();
    useEffect(() => {
        if (inView) {
            animation.start({
                opacity: 1, x: 0,
                transition: {
                    duration: 1
                }
            })
        }
        else {
            animation.start({ opacity: 0, x: -200 })
        }
    }, [inView])

    const [stt, setStt] = useState(false)
    return (
        <><Swipe />

            <div className="one">
                <FingerPrint />
            </div>

            <div ref={ref} className="secondpage">
                <motion.div animate={animation}
                    className="flex-cont-col">
                    {/* <div className="first">
                        <motion.img animate={{ y: [5, 0, 5] }} transition={{ repeat: Infinity, duration: 1 }} initial={{ y: 0 }} src={man} />
                    </div> */}
                    <Cat />
                    <div className="second">
                        <span>HTML</span>
                        <span>CSS</span>
                        <span>JS</span>
                        <span>REACT</span>
                    </div>

                    <div className="third"><h1 style={{ fontSize: "3rem" }}>hello !<br /> <span style={{ fontSize: "3rem" }}>i'm sai kiran</span> <br />I'm a Front End Developer/.</h1></div>
                </motion.div>
            </div>
            <div className="rct">
                <Rct />
            </div>
        </>
    )
}

export default Home