import Lottie from "lottie-react";
import home from "./images/home.json";
import profile from "./images/profile.json"
import sun from "./images/sun.json";
import fingerprint from "./images/fingerprint.json"
import swipe from "./images/swipe.json"
import cat from "./images/handshake.json"
import react from './images/react.json'
const style = {
    height: 100,
};
const stylehome = {
    height: 900,
}
const interactivity = {
    mode: "scroll",
    actions: [
        {

            visibility: [0 - 0.3], type: "stop",
        },
        {
            visibility: [0.3, 1],
            type: "seek",
            frames: [0, 450],
        },
    ],
};


const Home = () => {
    return (
        <Lottie
            animationData={home}
            style={style}
            loop={false}
        />
    );
};

export default Home;

export const Profile = () => {
    return (
        <Lottie
            animationData={profile}
            style={style}
            loop={false}
        />
    )
}

export const Sun = () => {
    return (
        <Lottie
            animationData={sun}
            style={style}
            loop={true}
        />
    )
}
export const FingerPrint = () => {
    return (
        <Lottie
            animationData={fingerprint}
            style={stylehome}
            interactivity={interactivity}
        />
    )
}
const styleswipe = {
    height: 500
}
export const Swipe = () => {
    return (
        <Lottie id="swipe"
            animationData={swipe}
            style={styleswipe}
            loop={true}
        />
    )
}
const stylecat = {
    height: 300,
}
const interactivitycat = {
    mode: "scroll",
    actions: [

        {
            visibility: [0, 1],
            type: "seek",
            frames: [0, 200],
        },
    ],
};

export const Cat = () => {
    return (
        <Lottie
            animationData={cat}
            style={stylecat}
            interactivity={interactivitycat}
        />
    )
}
const stylerct = {
    height: 400,
}
const interactivityrct = {
    mode: "scroll",
    actions: [
        {
            visibility: [0, 1],
            type: "seek",
            frames: [0, 310],
        },
    ],
};
export const Rct = () => {
    return (
        <Lottie
            animationData={react}
            style={stylerct}
            interactivity={interactivityrct}
        />
    )
}